
@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

/*nav*/
@media (min-width: 768px) {
  #Nav_bar{
      position:absolute;
      width:fit-content;
      align-items: center;
      margin-left: 36%;
      margin-top:2%;
      font-size: large;
    
  
  }


}


@media (max-width: 768px) {
  #Nav_bar{
      position:absolute;
      width:fit-content;
      align-items: center;
      margin-left: 20%;
      font-size: medium;
      margin-bottom: 0rem;
  
  }

  
}

















/*body*/
@media (min-width: 768px) {
  .profile{
    height: auto;
    position:absolute;
    margin-top: 8%;
    align-items: center;
    margin-left: 38%;
  }
  .my_img{
    height:12rem;
  }

  #link-icon{
    margin: 0 0 0 0% ;
    width:fit-content;
  }
  
  
  
}


@media (max-Width:768px) {
  .profile{
    height: auto;
    position:absolute;
    margin:10% 20% ;
   
    align-items: center;
  }
  
}
